import { createRouter, createWebHashHistory } from "vue-router";
/*
import ListPage from '../pokemon/pages/ListPage'
import AboutPage from '../pokemon/pages/AboutPage'
import PokemonPage from '../pokemon/pages/PokemonPage'
*/


const routes = [

    {
        path: "/",
        redirect: "ListaPokemon"
    },

    {
        path:'/Lista', 
        name: "ListaPokemon",
        component: () => 
        import(/*webpackChunkName: "ListPage" */'@/pokemon/pages/ListPage')

    },


    {
        path:'/about',
        component : () => 
        import(/*webpackChunkName: "AboutPage" */'@/pokemon/pages/AboutPage')
    },
    {
        path:'/:id' , 
        name:'pk-id',
        component: () =>
        import(
            /*webpackChunkName: "PokemonPage" */'@/pokemon/pages/PokemonPage'
        ),
        props: (route) => { 
        const  id  = Number(route.params.id);
        return isNaN(id) ? { id : 1 } : { id };
        } 
    },
    {
        path:'/:pathMatch(.*)*',
         component: () => 
         import(/*webpackChunkName: "NoPageFound" */'@/shared/pages/NoPageFound')
    }
]

const router = createRouter({
    history: createWebHashHistory(),
    routes
});

export default router